class GithubAPI {
    private _baseLink: string = 'https://api.github.com';
    private _baseOwnerName: string = 'RomanSavran';
    private _baseReposName: string = 'standards';
    private createURL(path: string): string {
        return `${this._baseLink}/repos/${this._baseOwnerName}/${this._baseReposName}/contents/${path}.jsonld?access_token=667dc3b34fa43816e4b3d2b5998f9b3359e3f5f8&ref=ontotest`;
    }
    private createPublicURL(path: string): string {
        return path;
    }

    getData = async (path: string) => {
        const url: string = process.env.NODE_ENV === 'production' ? this.createPublicURL(path) : this.createURL(path);
        const res = await fetch(url, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, received ${res.status}`);
        }

        return await res.json();
    }

    getVocabularyData = async (path: string) => {
        const vocabData = await this.getData(path);

        return vocabData;
    }
}

export default new GithubAPI();