import React from 'react';
import { NavLink } from 'react-router-dom';
import {
    Grid,
    makeStyles
} from '@material-ui/core';
import logo from '../../assets/logo.svg';

const useStyles = makeStyles((theme) => ({
    header: {
        padding: '15px 36px',
        background: 'rgb(8, 44, 70)'
    },
    logoWrapper: {
        padding: 5
    },
    img: {
        display: 'block',
        maxWidth: '100%',
        height: 'auto',
        opacity: '0.7'
    },
    ul: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        margin: 0,
        padding: 0,
        listStyle: 'none'
    },
    link: {
        position: 'relative',
        display: 'block',
        padding: 5,
        fontFamily: 'Roboto',
        fontSize: 16,
        fontStyle: 'normal',
        fontWeight: 400,
        lineHeight: '19px',
        color: '#fff',
        textDecoration: 'none',
        transition: '.1s linear',
        '&.active::before': {
            content: "''",
            position: 'absolute',
            bottom: 0,
            left: 0,
            width: '100%',
            height: 2,
            background: 'rgb(0, 149, 255)'
        },
        '&:hover': {
            color: 'rgb(0, 149, 255)'
        }
    }
}));

type Link = {
    label: string,
    to: string
}

const links: Link[] = [
    { label: 'Classes Higherarchy', to: '/classes-higherarchy' },
    { label: 'Classes Grid', to: '/classes-grid' },
    { label: 'Properties', to: '/properties-grid' }
]

const TopBar: React.FC = () => {
    const classes = useStyles();
    return (
        <div className={classes.header}>
            <Grid
                container
                spacing={3}
                alignItems="center"
            >
                <Grid
                    item
                    xs={7}
                >
                    <div className={classes.logoWrapper}>
                        <img
                            src={logo}
                            alt="Platform Of Trust"
                            className={classes.img}
                        />
                    </div>
                </Grid>
                <Grid
                    item
                    xs={5}
                >
                    <ul className={classes.ul}>
                        {links.map(link => (
                            <NavLink
                                className={classes.link}
                                key={link.to}
                                to={link.to}
                                exact
                            >
                                {link.label}
                            </NavLink>
                        ))}
                    </ul>
                </Grid>
            </Grid>
        </div>
    )
}

export default React.memo(TopBar);