import React, { useState, useContext, useMemo, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { RoutesContext } from '../context/RoutesContext';
import {
	Grid,
	makeStyles,
	createStyles,
	TextField
} from '@material-ui/core';
import Tree from '../components/Tree';
import { ClassItemType, buildTree, getRootNodes } from '../utils/helpers';
import { Base64 } from 'js-base64';
import Spinner from '../components/Spinner';
import ServiceAPI from '../services/api';
import RouterTabs from '../components/Tabs';
import { GeneralInformation, Context, ClassDefinition, Vocabulary } from '../components/Tabs/Components';

const tabTitles: string[] = [
	'General Information',
	'Context',
	'Vocabulary',
	'Class Definition',
	'Data Example'
]

type ClassesHigherarchyType = {
	classesData: ClassItemType[],
}

const useStyles = makeStyles((theme) =>
	createStyles({
		inputRoot: {
			width: '100%',
			paddingLeft: 15,
			paddingRight: 15,
			fontSize: 15,
			border: '1px solid rgb(196, 203, 217)',
			color: 'rgb(70, 69, 69)',
			boxSizing: 'border-box'
		}
	})
);

const ClassesDetails: React.FC<ClassesHigherarchyType> = ({ classesData }) => {
	const classes = useStyles();
	const [value, setValue] = useState({
		vocabData: null,
		loading: true
	})
	const [filter, handleFilterChange] = useState('');
	const { currentPath } = useContext(RoutesContext);
	const shouldTreeView: boolean = currentPath === 'classes-higherarchy';
	const location = useLocation();
	const path: string = location.pathname.split('/v1/Context/').pop() || '';

	useEffect(() => {
		window.scrollTo(0, 0);
		setValue({
			vocabData: null,
			loading: true
		})
		ServiceAPI.getVocabularyData(`/v1/Vocabulary/${path}`)
			.then(data => {
				setValue({
					vocabData: JSON.parse(Base64.decode(data.content)),
					loading: false
				})
			})
	}, [path]);

	const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { value } = event.target;
		handleFilterChange(value);
	}

	const classesTree = useMemo(() => buildTree(classesData), [classesData]);
	const rootNodes = useMemo(() => getRootNodes(classesTree), [classesTree]);
	const id: string = path.split('/').pop() || '';
	const isShowExtraTabs = path !== 'Identity' &&
		path !== 'Link' &&
		!path.includes('Technical') &&
		!path.includes('UnitOfMeasure');

	return (
		<Grid
			container
			spacing={shouldTreeView ? 3 : 0}
		>
			{shouldTreeView && (
				<Grid
					item
					xs={3}
				>
					<TextField
						className={classes.inputRoot}
						placeholder="Filter..."
						value={filter}
						onChange={handleChange}
						InputProps={{
							disableUnderline: true
						}}
					/>
					<Tree
						filter={filter.toLowerCase()}
						rootNodes={rootNodes}
						tree={classesTree}
					/>
				</Grid>
			)}
			<Grid
				item
				xs={shouldTreeView ? 9 : 12}
			>
				{value.loading ? <Spinner /> : (
					<RouterTabs
						tabTitles={tabTitles}
					>
						<GeneralInformation
							type="class"
							vocabData={value.vocabData}
							id={id}
						/>
						{isShowExtraTabs && <Context
							path={path}
						/>}
						{isShowExtraTabs && <Vocabulary
							path={path}
						/>}
						{isShowExtraTabs && <ClassDefinition
							path={path}
						/>}
						<div></div>
					</RouterTabs>
				)}
			</Grid>
		</Grid>
	)
}

export default ClassesDetails;