import React from 'react';
import {
	makeStyles,
	createStyles,
	Theme
} from '@material-ui/core';
import { has, get, values } from 'lodash';
import { ClassItemType } from '../../../utils/helpers';
import { useLocation, Link } from 'react-router-dom';
import CopyTooltip from '../../CopyTooltip';
import clsx from 'clsx';

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		h2: {
			fontSize: 30,
			fontWeight: 600,
			lineHeight: '37px',
			textTransform: 'capitalize'
		},
		descriptionWrapper: {
			display: 'flex',
			flexWrap: 'wrap'
		},
		descriptionItem: {
			width: '49%',
			marginRight: '2%',
			marginBottom: '2%',
			padding: '20px 15px',
			background: 'rgb(235, 240, 248)',
			boxSizing: 'border-box',
			overflow: 'hidden',
			textOverflow: 'ellipsis',
			'&:nth-child(2n)': {
				marginRight: 0
			}
		},
		itemTitle: {
			margin: 0,
			marginRight: 10,
			marginBottom: 20,
			fontSize: 23,
			fontWeight: 600,
			lineHeight: '28px'
		},
		text: {
			margin: 0,
			fontSize: 14,
			color: 'rgb(49, 49, 49)'
		},
		link: {
			fontSize: 14,
			color: 'rgb(0, 149, 255)'
		},
		li: {
			marginBottom: 15,
			padding: '2px 10px',
			fontSize: 14,
			background: 'rgb(196, 203, 216)',
			borderRadius: 3,
			color: 'rgb(49, 49, 49)'
		},
		ul: {
			display: 'flex',
			flexDirection: 'column',
			alignItems: 'flex-start'
		},
		ulLink: {
			fontSize: 14,
			color: 'inherit',
			textDecoration: 'none',
			'&:hover': {
				textDecoration: 'underline'
			}
		},
		tableDescription: {
			margin: 0,
			fontSize: 18,
			marginBottom: 25,
		},
		disabledClass: {
			opacity: '0.5'
		},
		table: {
			width: '100%',
			borderCollapse: 'collapse'
		},
		th: {
			padding: 15,
			fontSize: 16,
			fontWeight: 500,
			color: 'rgb(145, 157, 182)',
			textAlign: 'left'
		},
		tr: {
			borderBottom: '1px solid rgb(241, 242, 243)'
		},
		td: {
			padding: '25px 20px',
			fontSize: 14
		},
		copyWrapper: {
			display: 'flex',
			alignItems: 'center',
			marginBottom: 20
		},
		copyTitle: {
			marginBottom: 0
		},
		linkCls: {
			color: 'rgb(0, 123, 255)',
			textDecoration: 'none',
			'&:hover': {
				textDecoration: 'underline'
			}
		},
		linkDecor: {
			padding: 4,
			background: 'rgb(241, 242, 243)',
			borderRadius: 3,
			color: 'rgb(0, 149, 255)'
		}
	}),
);

type Properties = {
	[key: string]: {
		id: string,
		category: string,
		description: string,
		range: string,
		url: string
	}
}

type GeneralInformationTypes = {
	id: string,
	vocabData: any,
	type: 'class' | 'prop',
	classesData?: ClassItemType[]
}

type TableClasses = {
	th: string,
	table: string,
	td: string,
	tr: string,
	linkCls?: string,
	linkDecor?: string
}

function renderClassTable(classesData: ClassItemType[], classes: TableClasses) {
	return (
		<table className={classes.table}>
			<thead>
				<tr>
					<th className={classes.th}>Class</th>
					<th className={classes.th}>subClassOf</th>
					<th className={classes.th}>Comment</th>
				</tr>
			</thead>
			<tbody>
				{classesData.map(cls => {
					return (
						<tr
							className={classes.tr}
							key={cls.id}
						>
							<td className={classes.td}>
								<span className={classes.linkDecor}>
									<Link to={cls.url} className={classes.linkCls}>
										{cls.id}
									</Link>
								</span>
							</td>
							<td className={classes.td}>{cls.subClass}</td>
							<td className={classes.td}>{cls.comment}</td>
						</tr>
					)
				})}
			</tbody>
		</table>
	)
}

function renderPropTable(properties: Properties, classes: TableClasses) {
	return (
		<table className={classes.table}>
			<thead>
				<tr>
					<th className={classes.th}>Property</th>
					<th className={classes.th}>Category</th>
					<th className={classes.th}>Description</th>
					<th className={classes.th}>Range</th>
				</tr>
			</thead>
			<tbody>
				{values(properties).map(prop => {
					return (
						<tr
							className={classes.tr}
							key={prop.id}
						>
							<td className={classes.td}>
								<span className={classes.linkDecor}>
									<Link to={prop.url} className={classes.linkCls}>
										{prop.id}
									</Link>
								</span>
							</td>
							<td className={classes.td}>{prop.category}</td>
							<td className={classes.td}>{prop.description}</td>
							<td className={classes.td}>{prop.range}</td>
						</tr>
					)
				})}
			</tbody>
		</table>
	)
}

const GeneralInformation: React.FC<GeneralInformationTypes> = (props) => {
	const classes = useStyles();
	const {
		id,
		vocabData,
		classesData,
		type
	} = props;
	const location = useLocation();

	const properties = Object.keys(vocabData)
		.filter(key => vocabData[key]['@type'] === 'owl:DatatypeProperty')
		.reduce((acc, current) => {
			const prop = vocabData[current];
			const url: string = `/v1/${get(prop, '@id').split('pot:').pop()}`;
			return {
				...acc,
				[current]: {
					id: current,
					url,
					category: prop['subPropertyOf'] || '',
					description: has(prop, 'rdfs:comment') && has(prop, 'rdfs:comment.en-us') ? get(prop, 'rdfs:comment.en-us') : '',
					range: get(prop, 'domain.0') || ''
				}
			}
		}, {});

	const details = vocabData[id];

	const label: string = has(details, 'rdfs:label') && has(details, 'rdfs:label.en-us') ? get(details, 'rdfs:label.en-us') : 'Has no label';

	const description: string = has(details, 'rdfs:comment') && has(details, 'rdfs:comment.en-us') ? get(details, 'rdfs:comment.en-us') : 'Has no description';

	const superclasses: string[] = location.pathname
		.split('/')
		.filter(path => !['v1', 'context', '', id.toLowerCase()].includes(path.toLowerCase()));

	const uri: string = `https://standards.oftrust.net${location.pathname}`;

	const mappedDomain: string[] = type === 'prop' && has(details, 'domain') ? details.domain.map((domain: string) => domain.split('pot:').pop()) : [];

	return (
		<div>
			<h2 className={classes.h2}>{id}</h2>
			<div
				className={classes.descriptionWrapper}
			>
				<div
					className={classes.descriptionItem}
				>
					<div className={classes.copyWrapper}>
						<h4 className={clsx(classes.itemTitle, classes.copyTitle)}>Context URI</h4>
						<CopyTooltip
							copyText={uri}
						/>
					</div>
					<a
						className={classes.link}
						href={uri}
						target="_blank"
						rel="noopener noreferrer"
					>
						{uri}
					</a>
				</div>
				<div
					className={classes.descriptionItem}
				>
					<h4 className={classes.itemTitle}>Label</h4>
					<p className={classes.text}>{label}</p>
				</div>
				<div
					className={classes.descriptionItem}
				>
					<h4 className={classes.itemTitle}>Description</h4>
					<p className={classes.text}>{description}</p>
				</div>
				<div
					className={classes.descriptionItem}
				>
					<h4 className={classes.itemTitle}>Superclasses ({superclasses.length})</h4>
					<ul
						className={classes.ul}
					>
						{superclasses.map((item, index, array) => {
							const to: string = array.slice(0, index + 1).join('/');
							const mainPath = type === 'class' ? '/v1/Context/' : '/v1/'

							return (
								<li
									className={classes.li}
									key={to}
								>
									<Link
										className={classes.ulLink}
										to={`${mainPath}${to}`}
									>
										{item}
									</Link>
								</li>
							)
						})}
					</ul>
				</div>
			</div>
			<div>
				<h4 className={classes.itemTitle}>Usage</h4>
				{type === 'prop' ? null : <p className={classes.tableDescription}>Instances of {id} can have the following properties:</p>}
				{type === 'prop' && classesData ? renderClassTable(
					classesData.filter(cls => mappedDomain.includes(cls.id)),
					classes
				) : renderPropTable(properties, classes)}
			</div>
		</div>
	)
}

export default React.memo(GeneralInformation);