import React, { useState } from 'react';
import {
	Table as MuiTable,
	TableHead as MuiTableHead,
	TableBody as MuiTableBody,
	TableCell as MuiTableCell,
	TableRow as MuiTableRow,
	TextField,
	makeStyles,
	createStyles,
} from '@material-ui/core';
import { Link } from 'react-router-dom';
import SearchIcon from '@material-ui/icons/Search';
import { ClassItemType, PropertyItemType } from '../../utils/helpers';
import _ from 'lodash';

type Header = {
	id: string,
	label: string
}

type TableProps = {
	headers: Header[],
	data: any
}

function createState(headers: Header[]): { [key: string]: string } {
	return headers.reduce((acc, current) => {
		return { ...acc, [current.id]: '' }
	}, {})
}

const useStyles = makeStyles(() =>
	createStyles({
		table: {
			width: '100%',
			border: '1px solid rgb(124, 129, 141)'
		},
		th: {
			padding: 15,
			fontSize: 12,
			fontWeight: 600,
			fontFamily: 'Montserrat, sans-serif',
			border: 'none',
			borderRight: '1px solid rgb(224, 224, 224)',
			'&:last-child': {
				borderRight: 'none'
			}
		},
		inputTd: {
			padding: 0,
			border: '1px solid rgb(12, 118, 203)'
		},
		input: {
			width: '100%',
			height: 40,
			fontSize: 12,
			fontFamily: 'Montserrat, sans-serif',
			padding: '0 5px 0 10px',
		},
		inputRoot: {
			width: '100%'
		},
		searchRoot: {
			display: 'flex',
			justifyContent: 'space-between',
			alignItems: 'center'
		},
		searchIcon: {
			width: 18,
			height: 18,
			color: 'rgb(124, 129, 141)',
			paddingRight: 10
		},
		textRow: {
			'&:nth-child(2n)': {
				background: 'rgb(245, 245, 245)'
			}
		},
		mainTd: {
			padding: 15,
			fontSize: 12,
			fontFamily: 'Montserrat, sans-serif',
			border: 'none',
			borderRight: '1px solid rgb(224, 224, 224)',
			'&:last-child': {
				borderRight: 'none'
			}
		},
		link: {
			color: 'rgb(0, 123, 255)',
			textDecoration: 'none',
			'&:hover': {
				textDecoration: 'underline'
			}
		}
	})
);

const Table: React.FC<TableProps> = (props) => {
	const classes = useStyles();
	const {
		headers,
		data
	} = props;
	const [searchProps, setSearchProps] = useState(createState(headers));

	const handleChangeSearchProps = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { name, value } = event.target;
		setSearchProps(prevProps => ({
			...prevProps,
			[name]: value
		}))
	}

	const filterObj: {[key: string]: string} = Object.keys(searchProps)
		.filter(key => searchProps[key] !== '')
		.reduce((acc, current) => ({...acc, [current]: searchProps[current]}), {});

	const filterFunction = (element: ClassItemType): boolean => {
		return Object.keys(filterObj)
			.every(key => 
				_.get(element, key)
				.toLowerCase()
				.includes(filterObj[key])
			);
	}

	return (
		<MuiTable className={classes.table}>
			<MuiTableHead>
				<MuiTableRow>
					{headers.map(item => {
						return (
							<MuiTableCell
								className={classes.th}
								key={item.id}
							>
								{item.label}
							</MuiTableCell>
						)
					})}
				</MuiTableRow>
			</MuiTableHead>
			<MuiTableBody>
				<MuiTableRow>
					{headers.map(item => {
						return (
							<MuiTableCell
								className={classes.inputTd}
								key={item.id}
							>
								<div className={classes.searchRoot}>
									<TextField
										className={classes.inputRoot}
										name={item.id}
										placeholder="Search"
										onChange={handleChangeSearchProps}
										value={searchProps[item.id]}
										InputProps={{
											disableUnderline: true,
											className: classes.input
										}}
									/>
									<SearchIcon className={classes.searchIcon} />
								</div>
							</MuiTableCell>
						)
					})}
				</MuiTableRow>
				{
					_.chain(data)
						.filter(filterFunction)
						.sortBy('id')
						.map(element => {
							return (
								<MuiTableRow
									className={classes.textRow}
									key={element.url}
								>
									{headers.map(header => {
										return (
											<MuiTableCell
												className={classes.mainTd}
												key={header.id}
											>
												{header.id === 'id' ? (
													<Link
														className={classes.link}
														to={element.url}
													>
														{_.get(element, `${header.id}`)}
													</Link>
												) : _.get(element, `${header.id}`)}
											</MuiTableCell>
										)
									})}
								</MuiTableRow>
							)
						})
						.value()
					}
			</MuiTableBody>
		</MuiTable>
	)
}

export default Table;