import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import {
	Tabs,
	Tab,
	makeStyles,
	createStyles
} from '@material-ui/core';

const useStyles = makeStyles((theme) =>
	createStyles({
		tabRoot: {
			minWidth: 'auto',
			textTransform: 'none',
			padding: '15px 25px',
			color: '#4C4C51',
			transition: 'color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
			[theme.breakpoints.down('xs')]: {
				padding: '10px'
			}
		},
		tabWrapper: {
			position: 'relative',
			zIndex: 999,
			fontSize: 12,
			fontWeight: 600,
			lineHeight: '12px',
			fontFamily: 'Montserrat'
		},
		tabSelected: {
			color: '#fff',
			[theme.breakpoints.down('xs')]: {
				background: '#0095FF',
				transition: 'all 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
				outline: 'none'
			}
		},
		tabsRoot: {
			borderBottom: '2px solid #0095ff',
			[theme.breakpoints.down('xs')]: {
				border: 'none'
			}
		},
		tabsIndicator: {
			height: '100%',
			background: '#0095FF',
			[theme.breakpoints.down('xs')]: {
				display: 'none'
			}
		},
		tabsFlexContainer: {
			[theme.breakpoints.down('xs')]: {
				display: 'flex',
				flexWrap: 'wrap'
			}
		}
	})
);

type RouterTabsTypes = {
	tabTitles: string[],
	children: React.ReactNode[]
}

const RouterTabs: React.FC<RouterTabsTypes> = (props) => {
	const classes = useStyles();
	const {
		tabTitles,
		children
	} = props;
	const [value, setValue] = useState(0);
	const location = useLocation();
	const id: string = location.pathname.split('/').pop() || '';

	const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
		setValue(newValue)
	}

	return (
		<>
			<Tabs
				classes={{
					root: classes.tabsRoot,
					indicator: classes.tabsIndicator,
					flexContainer: classes.tabsFlexContainer
				}}
				value={value}
				onChange={handleChange}
				aria-label="Tabs"
			>
				{tabTitles.map(title => {
					const isDisabled = title !== 'General Information' && (
						['identity', 'link'].includes(id.toLowerCase()) || 
						location.pathname.includes('PhysicalProperty') ||
						location.pathname.includes('Technical') ||
						location.pathname.includes('UnitOfMeasure')
					);

					return (
						<Tab
							disabled={isDisabled}
							key={title}
							label={title}
							classes={{
								root: classes.tabRoot,
								wrapper: classes.tabWrapper,
								selected: classes.tabSelected
							}}
						/>
					)
				})}
			</Tabs>
			<div>
				{React.Children.toArray(children).filter((child, idx) => idx === value)}
			</div>
		</>
	)
}

export default React.memo(RouterTabs);