import React, { useState } from 'react';
import {
	Collapse,
	makeStyles,
	createStyles,
	IconButton
} from '@material-ui/core';
import clsx from 'clsx';
import CopyTooltip from '../../CopyTooltip';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import download from 'downloadjs';
import Markdown from 'react-markdown';

const useStyles = makeStyles(() => 
	createStyles({
		root: {
			marginTop: 30,
		},
		itemDescription: {
			marginBottom: 30,
			padding: '20px 15px',
			background: 'rgb(235, 240, 248)',
			overflow: 'hidden',
			textOverflow: 'ellipsis'
		},
		copyWrapper: {
			display: 'flex',
			alignItems: 'center',
			marginBottom: 20
		},
		copyTitle: {
			margin: 0,
			marginRight: 10,
			fontSize: 23,
			fontWeight: 600,
			lineHeight: '28px',
			marginBottom: 0
		},
		link: {
			fontSize: 14,
			color: 'rgb(0, 149, 255)'
		},
		iconButton: {
			padding: 2
		},
		h3: {
			margin: 0,
			marginBottom: 10,
			fontSize: 23,
			fontWeight: 600,
			lineHeight: '28px'
		},
		chevronWrapper: {
			display: 'flex',
			justifyContent: 'space-between',
			alignItems: 'center'
		},
		chevron: {
			fontSize: '2.3rem',
			color: '#000',
			transition: '.2s linear'
		},
		chevronOpen: {
			transform: 'rotate(180deg)',
			transition: '.2s linear'
		},
		definitionTop: {
			display: 'flex',
			justifyContent: 'space-between',
			alignItems: 'center',
			padding: '13px 23px',
			background: 'rgb(21, 72, 109)'
		},
		definitionTopLeft: {
			fontSize: 14,
			color: '#fff'
		},
		pre: {
			margin: 0,
			padding: 20,
			fontSize: 14,
			fontFamily: 'Roboto',
			fontWeight: 300,
			color: 'rgb(12, 77, 123)'
		},
		body: {
			marginBottom: 20,
			background: 'linear-gradient(0deg,rgb(235, 240, 248),rgb(235, 240, 248)),rgb(29, 37, 72)'
		},
		downloadBtn: {
			padding: '4px 9px',
			fontSize: 14,
			background: 'rgb(31, 91, 134)',
			color: 'rgb(255, 255, 255)',
			cursor: 'pointer',
			textAlign: 'center',
			transition: '.2s linear',
			'&:hover': {
				background: 'rgb(32, 178, 170)'
			}
		},
		markdown: {
			padding: 20,
			border: '2px solid rgb(235, 240, 248)',
			'& ul': {
				marginTop: 0,
				marginBottom: '1rem'
			},
			'& p': {
				marginTop: 0,
				marginBottom: '1rem'
			},
			'& h1': {
				marginTop: 0
			},
			'& h2': {
				marginTop: 0,
				fontSize: '2rem',
				marginBottom: '.5rem',
				fontWeight: 500,
				lineHeight: '1.2'
			},
			'& code': {
				fontSize: 16,
				color: 'rgb(232, 62, 140)',
				wordBreak: 'break-word'
			},
			'& a': {
				color: 'rgb(0, 123, 255)',
				textDecoration: 'none',
				'&:hover': {
					textDecoration: 'underline'
				}
			},
			'& pre': {
				display: 'block',
				marginTop: 0,
				marginBottom: '1rem',
				overflow: 'auto',
				padding: 10,
				fontFamily: 'Roboto',
				fontSize: 14,
				fontWeight: 300,
				background: 'rgb(235, 240, 248)',
				'& code': {
					fontFamily: 'SFMono-Regular,Menlo,Monaco,Consolas,Liberation Mono,Courier New,monospace',
					color: 'rgb(0, 0, 139)'
				}
			},
			'& strong': {
				fontWeight: 600
			}
		}
	})
);

type MainTabProps = {
	uri: string,
	sloc: number,
	size: string,
	content: any,
	fileName: string,
	markdown: any,
}

const MainTab: React.FC<MainTabProps> = (props) => {
	const classes = useStyles();
	const {
		uri,
		sloc,
		size,
		content,
		fileName,
		markdown
	} = props;
	const [showDetails, setShowDetails] = useState(true);

	const handleChangeShowDetails = () => {
		setShowDetails(prevState => !prevState);
	}

	const downloadFile = () => {
		const downloadData = JSON.stringify(content, undefined, 2);

		download(downloadData, `${fileName}.json`, 'application/json');
	}

	return (
		<div className={classes.root}>
			<div className={classes.itemDescription}>
				<div className={classes.copyWrapper}>
					<h4 className={classes.copyTitle}>URI</h4>
					<CopyTooltip
						copyText={uri}
					/>
				</div>
				<a
					className={classes.link}
					href={uri}
					target="_blank"
					rel="noopener noreferrer"
				>
					{uri}
				</a>
			</div>
			<div className={classes.chevronWrapper}>
				<h3 className={classes.h3}>Implementation</h3>
				<IconButton 
					className={classes.iconButton}
					onClick={handleChangeShowDetails}
				>
					<KeyboardArrowDownIcon className={clsx(classes.chevron, {
						[classes.chevronOpen]: !showDetails
					})}/>
				</IconButton>
			</div>
			<div className={classes.body}>
				<div className={classes.definitionTop}>
					<div className={classes.definitionTopLeft}>
						<span>{sloc} lines ({sloc} sloc)</span>
						{" "}
						<span>{`${size} KB`}</span>
					</div>
					<div>
						<span 
							className={classes.downloadBtn}
							onClick={downloadFile}
						>
							Download
						</span>
					</div>
				</div>
				<Collapse
					in={showDetails}
					timeout={350}
				>
					<pre className={classes.pre}>{JSON.stringify(content, undefined, '\t')}</pre>
				</Collapse>
			</div>
			<div className={classes.markdown}>
				<Markdown source={markdown}/>
			</div>
		</div>
	)
}

export default MainTab;