import React, { useState, useEffect } from 'react';
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Redirect
} from 'react-router-dom';
import { RoutesContext } from './context/RoutesContext';
import { Base64 } from 'js-base64';
import {
	ClassesHigherarchy,
	ClassesGrid,
	ClassesDetails,
	PropertiesGrid,
	PropertyDetails
} from './routes';
import {
	makeStyles,
	createStyles
} from '@material-ui/core';
import ServiceAPI from './services/api';
import Spinner from './components/Spinner';
import {
	modifyClass,
	modifyProperties,
	ClassItemType,
	PropertyItemType
} from './utils/helpers';
import TopBar from './components/TopBar';
import Breadcrumbs from './components/Breadcrumbs';

const useStyles = makeStyles(() =>
	createStyles({
		container: {
			padding: 20
		}
	})
);

export function App() {
	const classes = useStyles();
	const [value, setValue] = useState({
		data: [],
		loading: true,
		hasError: false
	});
	const [currentPath, setCurrentPath] = useState
		<'classes-higherarchy' |
			'classes-grid' |
			'properties-grid'>
		('classes-higherarchy');

	const handleChangeCurrentPath = (path: 'classes-higherarchy' | 'classes-grid' | 'properties-grid') => {
		setCurrentPath(path);
	}

	const query = process.env.NODE_ENV === 'production' ? '/v1/Vocabulary/' : 'v1/Ontology/pot';

	useEffect(() => {
		let mounted = false;

		if (!mounted) {
			ServiceAPI.getData(query)
				.then(data => {
					setValue(prevValue => ({
						...prevValue,
						data: JSON.parse(Base64.decode(data.content)),
						loading: false
					}))
				})
				.catch(err => {
					console.error(err);
					setValue({
						data: [],
						loading: false,
						hasError: true
					})
				})
		}
	}, []);

	if (value.loading) return <Spinner />

	const { data } = value;
	const classesData: ClassItemType[] = [];
	const propertiesData: PropertyItemType[] = [];

	data.forEach(element => {
		if ('@type' in element) {
			const type: string = element['@type'][0];

			if (type.includes('owl#Class')) {
				classesData.push(modifyClass(element))
			} else if (type.includes('owl#DatatypeProperty')) {
				propertiesData.push(modifyProperties(element))
			}
		}
	});

	return (
		<Router>
			<RoutesContext.Provider
				value={{
					currentPath,
					handleChangeCurrentPath
				}}
			>
				<TopBar />
				<Breadcrumbs />
				<div className={classes.container}>
					<Switch>
						<Route path="/classes-higherarchy" exact render={() => (
							<ClassesHigherarchy
								classesData={classesData}
							/>
						)} />
						<Route path="/classes-grid" exact render={() => (
							<ClassesGrid 
								classesData={classesData}
							/>
						)}/>
						<Route path="/v1/Context/*" render={() => (
							<ClassesDetails 
								classesData={classesData}
							/>
						)}/>
						<Route path="/properties-grid" exact render={() => (
							<PropertiesGrid 
								propertiesData={propertiesData}
							/>
						)}/>
						<Route path="/v1/*" exact render={() => (
							<PropertyDetails classesData={classesData}/>
						)}/>
						<Redirect to="/classes-higherarchy"/>
					</Switch>
				</div>
			</RoutesContext.Provider>
		</Router>

	)
}
