import React, { useState, useEffect } from 'react';
import Spinner from '../../Spinner';
import SystemAPI from '../../../services/api';
import MainTab from './MainTab';
import { Base64 } from 'js-base64';
import mdFileCV from '../../../assets/context.md';

type ContextProps = {
    path: string
}

type ContextStateTypes = {
    content: any,
    size: string,
    sloc: number,
    loading: boolean
}

const Context: React.FC<ContextProps> = (props) => {
    const {
        path
    } = props;
    const [value, setValue] = useState<ContextStateTypes>({
        content: '',
        size: '',
        sloc: 0,
        loading: true
    });
    const [mdFile, setMDFile] = useState<string | null>(null);
    const query = `v1/Context/${path}`;
    const id = path.split('/').pop();

    useEffect(() => {
        let mounted = false;

        SystemAPI.getData(query)
            .then(data => {
                if (!mounted) {
                    const content = Base64.decode(data.content);
                    setValue({
                        content: JSON.parse(content),
                        size: (data.size / 1024).toFixed(2),
                        sloc: content.split('\n').length,
                        loading: false
                    })
                }
            })

        return () => {
            mounted = true;
        }
    }, [query]);

    useEffect(() => {
        fetch(mdFileCV)
            .then(response => {
                return response.text();
            })
            .then(markdownText => {
                setMDFile(markdownText)
            })
    }, []);

    if (value.loading) return <Spinner />

    return (
        <MainTab
            uri={`https://standards.oftrust.net/${query}`}
            size={value.size}
            sloc={value.sloc}
            content={value.content}
            fileName={`Context-${id}`}
            markdown={mdFile}
        />
    )
}

export default Context;