import {values, get, has} from 'lodash';

export type ClassItemType = {
	url: string,
	id: string,
	type: string,
	label?: string,
	comment?: string,
	subClass?: string
}

export type PropertyItemType = {
	url: string,
	id: string,
	category?: string,
	comment?: string,
	label?: string,
	range?: string,
	domain?: string
}

export type NodeType = {
    path: string,
    children: string[],
    root?: boolean
}

export function modifyClass(item: any) {
    const splitter = 'v1/Vocabulary/';
    const url: string = item['@id'].split(splitter).pop();
    const id: string = url.split('/').pop() || '';
    const type: string = item['@type'][0].split('http://www.w3.org/2002/07/').pop();
    const label: string = 'http://www.w3.org/2000/01/rdf-schema#label' in item ? item['http://www.w3.org/2000/01/rdf-schema#label'].find((k: any) => k['@language'] === 'en-us')['@value'] : '';
    const comment: string = 'http://www.w3.org/2000/01/rdf-schema#comment' in item ? item['http://www.w3.org/2000/01/rdf-schema#comment'].find((k: any) => k['@language'] === 'en-us')['@value'] : '';
    const subClass: string = 'http://www.w3.org/2000/01/rdf-schema#subClassOf' in item ? item['http://www.w3.org/2000/01/rdf-schema#subClassOf'].map((k: any) => k['@id'].split(splitter).join('').split('/').pop()).join(', ') : '';

    return {
        url: `/v1/Context/${url}`,
        id,
        type,
        label,
        comment,
        subClass
    }
}

export function modifyProperties(item: any) {
    const splitter = 'v1/Vocabulary/';
    const url: string = item['@id'].split(splitter).pop();
    const id: string = url.split('/').pop() || '';
    const category: string = has(item, 'http://www.w3.org/2000/01/rdf-schema#subPropertyOf') ? item['http://www.w3.org/2000/01/rdf-schema#subPropertyOf'][0]['@id'] === 'http://www.w3.org/2002/07/owl#topDataProperty' ? 
        '' : item['http://www.w3.org/2000/01/rdf-schema#subPropertyOf'][0]['@id'].split(splitter).pop() : '';
    const comment: string = has(item, 'http://www.w3.org/2000/01/rdf-schema#comment') ? item['http://www.w3.org/2000/01/rdf-schema#comment'][0]['@value'] : '';
    const label: string = 'http://www.w3.org/2000/01/rdf-schema#label' in item ? item['http://www.w3.org/2000/01/rdf-schema#label'].find((k: any) => {
        return k['@language'] === 'en-us' || k['@language'] === 'fi-fi'
    })['@value'] : '';
    const range: string = 'http://www.w3.org/2000/01/rdf-schema#range' in item ? item['http://www.w3.org/2000/01/rdf-schema#range'][0]['@id'].split('http://www.w3.org/2001/XMLSchema#').pop() : '';
    const domain: string = 'http://www.w3.org/2000/01/rdf-schema#domain' in item ? item['http://www.w3.org/2000/01/rdf-schema#domain'].map((k: any) => k['@id'].split(splitter).join('').split('/').pop()).join(', ') : '';

    return {
        url: `/v1/${url}`,
        id,
        category,
        comment,
        label,
        range,
        domain,
    }
}

export function getRootNodes(data: {[key: string]: NodeType}) {
    return values(data).filter(node => node.root === true);
}

export function buildTree(list: ClassItemType[]) {
    return list.reduce((acc: any, current) => {
        const currentId: string = current.id;
        if (current.subClass) {
            let parents: string[] = current.subClass.split(', ');

            return parents.reduce((r: any, c: string) => {
                return c in r ? {
                    ...r,
                    [c]: {
                        ...r[c],
                        children: [
                            ...r[c].children,
                            currentId
                        ]
                    },
                    [currentId]: currentId in r ? r[currentId] : {path: current.url, children: []}
                } : {
                    ...r,
                    [c]: {
                        path: c,
                        children: [currentId]
                    },
                    [currentId]: currentId in r ? r[currentId] : {path: current.url, children: []}
                }
            }, acc)
        }

        return currentId in acc ? {
            ...acc,
            [currentId]: {
                ...acc[currentId],
                root: true
            }
        } : {
            ...acc,
            [currentId]: {
                path: current.url,
                children: [],
                root: true
            }
        }
    }, {});
}

export function parentsToState(data: NodeType[], initStatus: boolean): {[key: string]: boolean} | null {
    if (!data.length) return null;

    return data.reduce((acc, item) => {
        return {
            ...acc,
            [item.path]: initStatus
        }
    }, {});
}

export function getChildNodes(roots: any, node: NodeType) {
    return node.children.length ? node.children.map((path: string) => roots[path]) : [];
}