import React, {useState, useEffect} from 'react';
import ServiceAPI from '../services/api';
import {GeneralInformation} from '../components/Tabs/Components';
import { Base64 } from 'js-base64';
import {useLocation} from 'react-router-dom';
import Spinner from '../components/Spinner';
import {ClassItemType} from '../utils/helpers';

type PropertyDetailsTypes = {
    classesData: ClassItemType[]
}

const PropertyDetails: React.FC<PropertyDetailsTypes> = ({classesData}) => {
    const [value, setValue] = useState({
        data: null,
        loading: true
    });
    const location = useLocation();
    const path = location.pathname.split('/').filter(k => k !== 'v1').join('/');
    const id = path.split('/').pop() || '';

    useEffect(() => {
        ServiceAPI.getData(`/v1/Vocabulary${path}`)
            .then(data => {
                setValue({
                    data: JSON.parse(Base64.decode(data.content)),
                    loading: false
                })
            })
    }, [path]);

    if (value.loading) return <Spinner />

    return (
        <GeneralInformation 
            type="prop"
            vocabData={value.data}
            id={id}
            classesData={classesData}
        />
    )
};

export default PropertyDetails;